body {
  background-image: linear-gradient(#90909066, #908c8c66), url("https://firebasestorage.googleapis.com/v0/b/avanceradjs-slutprojekt-fs.appspot.com/o/background.jpg?alt=media&token=b821d440-2379-4816-9c41-000017de522b");
}

* {
  margin: 0;
  padding: 0;
}

html {
  font-family: Arial, Helvetica, sans-serif;
}

.flexBox {
  max-width: 58rem;
  color: #000;
  background: radial-gradient(50% 50%, #d9d9d9 0%, #d9d9d9e8 79.5%, #d9d9d9d9 96.13%, #d9d9d9cc 100%);
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  margin: 7rem auto 10rem;
  padding: 10px;
  display: flex;
  box-shadow: 0 4px 4px #00000040;
}

.product {
  filter: drop-shadow(0 6px 6px #00000080);
  text-transform: uppercase;
  width: 22rem;
  height: 5rem;
  background: #fffffff2;
  border: 10px solid #dbdada;
  border-radius: 5px;
  gap: 1rem;
  margin: 1rem;
  padding: 1rem;
  font-family: "Cursive", Lucida Handwriting;
  font-size: 14px;
  font-style: normal;
  font-weight: 400;
  line-height: 25px;
  display: flex;
}

.imgDiv {
  height: 5rem;
  width: 6rem;
  background-size: 125%;
}

.product h2 {
  font-size: 15px;
  font-weight: 2000;
}

.product #price {
  font-size: 13px;
  font-weight: 400;
}

.navBar {
  width: 100%;
  height: 5rem;
  z-index: 4;
  background: linear-gradient(#d8d8d8ed 83.85%, #d8d8d8e3 96.34%, #d8d8d8 96.35%, #d8d8d84f 100%);
  flex-direction: row;
  justify-content: center;
  gap: 20%;
  display: flex;
  position: sticky;
  top: 0%;
}

.navBar button {
  text-transform: uppercase;
  width: 7rem;
  max-width: 20%;
  height: 90%;
  border-radius: 60%;
  margin-top: 2px;
  font-family: "Cursive", Lucida Handwriting;
  font-size: 14px;
  font-style: normal;
  font-weight: 400;
  line-height: 25px;
  position: relative;
}

.navBar img {
  height: 100%;
}

.shoppingCart img {
  width: 50%;
  opacity: .8;
  border-radius: 40%;
  position: relative;
  inset: 0% 30% 10% 0%;
}

#shoppingCartBtn {
  background-image: url("https://firebasestorage.googleapis.com/v0/b/avanceradjs-slutprojekt-fs.appspot.com/o/pngegg.png?alt=media&token=67a49025-4486-49b2-a873-6317d5fbe4be");
  background-position: 45% 75%;
  background-repeat: no-repeat;
  background-size: 40%;
}

#numOfItemsInCart {
  width: 2rem;
  background-color: #fcfbfb;
  border-radius: 50%;
  position: relative;
  inset: -20% 0% 0% 45%;
}

.inStockAndAddToCart {
  color: #6bc133;
  gap: 5px;
  display: flex;
}

.footer {
  text-transform: uppercase;
  width: 100%;
  height: 5rem;
  background: linear-gradient(180deg, #d7d6d5ed 83.85%, #d7d6d5e3 96.34%, #d7d6d5 #d7d6d54f 100%);
  background-color: #d7d6d5;
  justify-content: center;
  align-items: center;
  gap: 20%;
  font-family: "Cursive", Lucida Handwriting;
  font-size: 14px;
  font-style: normal;
  font-weight: 400;
  line-height: 25px;
  display: flex;
  position: fixed;
  bottom: 0%;
  overflow: hidden;
}

.footer img {
  height: 100%;
  position: relative;
}

.footer img #socialButton {
  height: 80%;
}

.footer section {
  position: fix;
  height: 100%;
  width: 10rem;
  flex-flow: column;
  justify-content: center;
  align-items: center;
  display: flex;
}

.footer p {
  font-size: 10px;
}

